import { Vazirmatn } from 'next/font/google'
const vazir = Vazirmatn({ subsets: ['latin'] })
import "./globals.css"
import { channel } from 'diagnostics_channel'
export default function layout({ children }: {
    children: React.ReactNode
}) {
    return (
        <html lang="en">
            <body className={vazir.className}>
                <nav className="bg-blue-500 text-white mb-12 p-4 text-center">
                    <h1 className="text-4xl font-bold">سلام This is the Header</h1>
                </nav>
            {channel}
                <footer className="bg-gray-500 mt-12 text-white p-4 text-center">
                    <h1 className="text-4xl font-bold">This is the Footer</h1>
                </footer>
            </body>
        </html>
    )
}
